.LoadingTodo-container {
    /* position: relative; */
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin: 10px 0;
    box-shadow: 0px 5px 50px rgba(32, 35 41 0.15);
    border-radius: 10px;
    padding: 16px 32px;
  }
  
  .LoadingTodo-text {
    /* margin: 24px 0 24px 24px; */
    /* Width: calc(100% - 120px); */
    font-size: 18px;
    /* line-height: 24px; */
    /* font-weight: 400; */
  }
  
  .LoadingTodo-completeIcon,
  .LoadinTodo-deleteIcon {
    cursor: pointer;
    /* display: flex; */
    /* justify-content: center; */
    /* align-items: center; */
    border-radius: 20px;
    height: 18px;
    width: 18px;
    /* font: size 1.2rem; */
  }
  
  /* .LoadingTodo-completeIcon {
    position: absolute;
     left: 12px;
  } */
  
  /* .LoadinTodo-deleteIcon {
    position: absolute;
    top: -24px;
    right: 0;
  } */
  
  .LoadingTodo-container,
  .LoadingTodo-completeIcon,
  .LoadinTodo-deleteIcon {
    background: linear-gradient(90deg, rgba(250, 250, 250, 1), rgb(200, 199, 199));
    background-size: 400% 400%;
    animation: loadingAnimation 3s ease-in-out infinite;
  }
    
  @keyframes loadingAnimation {
    0% {
      background-position: 0% 50%;
    }
    50% {
      background-position: 100% 50%;
    }
    100% {
      background-position: 0% 50%;
    }
  };